import { useState } from 'react';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { Container, Input } from 'semantic-ui-react';
import { useQRCode } from 'next-qrcode';

/**
 * QRJSX
 *
 * @param param0
 * @returns
 */
const QRCodeCreate = () => {
  const [text, setText] = useState<string>('');
  const { Canvas } = useQRCode();

  return (
    <>
      <HelmetProvider>
        <Helmet>
          <title>QR | JavaScript Tools</title>
          <meta
            name="description"
            content="QRReact"
          />
        </Helmet>
      </HelmetProvider>
      <Container>
        <p>
          <Input
            fluid
            value={text}
            onChange={(e: any) => setText(e.target.value)}
            placeholder="QR"
          />
        </p>
        {text !== '' && (
          <Canvas
            text={text} // QR
            options={{
              // 
              type: 'image/webp', //  pngjpegwebp
              quality: 0.95, //  01
              level: 'M', //  low(L)mediumquartile()high(H)
              margin: 3, // 
              scale: 4, // 1px
              width: 200, // ()
            }}
          />
        )}
      </Container>
    </>
  );
};

export default QRCodeCreate;
